import store from '@/store'
import { getPlatform, isNativeClient } from '@/utils/helper'

/**
 *
 * @param {*} index 012
 */
export const hasShowGuideInstall = index => {
  const systemConfig = store.state.base?.systemConfig || {}
  const { force_install_app_switch = [0, 0, 0] } = systemConfig
  const flag = force_install_app_switch[index]
  const platform = store.state.base?.platform || ''
  const isIosDesktopWebApp = () => {
    return platform === 'ios' && 'standalone' in window.navigator && window.navigator['standalone']
  }
  const isApp = store.state.base?.isApp
  //    
  if (!flag || isNativeClient()) return false
  // ios
  return isApp && !isIosDesktopWebApp()
}
