const strpad = str => {
  str = String(str)
  if (str.length == 1) {
    return '0' + str
  } else {
    return str
  }
}

/**
 * 
 * @param {*} date 2022-12-222022/12/22
 * @param {*} pattern  12022-12-22 22022/12/22 329/12/22 4291222
 */
export function getFormatDate({ date = new Date(), pattern = 1 }) {
  if (!date) return ''
  let newDate = date
  if (typeof newDate == 'string') newDate = new Date(date.replace(/\-/g, '\/'))
  if (typeof newDate == 'number') newDate = new Date(date)

  let y = newDate.getFullYear()
  let m = newDate.getMonth() + 1
  m = m < 10 ? '0' + m : m
  let d = newDate.getDate()
  d = d < 10 ? '0' + d : d
  let hh = newDate.getHours()
  hh = hh < 10 ? '0' + hh : hh
  let mm = newDate.getMinutes()
  mm = mm < 10 ? '0' + mm : mm
  let time = `${y}-${m}-${d}`
  switch (Number(pattern)) {
    case 1:
      time = `${y}-${m}-${d}`
      break
    case 2:
      time = `${y}/${m}/${d}`
      break
    case 3:
      y = String(y)
      y = y.substring(2)
      time = `${d}/${m}/${y}`
      break
    case 4:
      y = String(y)
      y = y.substring(2)
      time = `${y}${m}${d}`
      break
    case 5:
      time = `${m}-${d} ${hh}:${mm}`
      break
  }

  return time
}
/**
 * 
 * @param {String} time 2021-07-16		// 
 * @param {String} appoint 2021-07-16	// 
 */
export function getTimeStamp(time = '', appoint = '') {
  if (!time || !appoint) return false

  let newTime = time
  let newAppoint = appoint

  if (typeof newTime == 'string') newTime = new Date(newTime.replace(/\-/g, '\/'))
  if (typeof newTime == 'number') newTime = new Date(newTime)
  if (typeof newAppoint == 'string') newAppoint = new Date(newAppoint.replace(/\-/g, '\/'))
  if (typeof newAppoint == 'number') newAppoint = new Date(newAppoint)

  const time1 = new Date(newTime).getTime()
  const time2 = new Date(newAppoint).getTime()

  return time1 >= time2 ? true : false
}
/**
 * @description , 
 * timestamp: 
 * formato: 
 * interval:  
 */
export function useTimestamp(timestamp, formato = 'yyyy-MM-dd HH:mm:ss', interval = '-') {
  let time = ''
  // 
  if (timestamp) {
    if (typeof timestamp == 'string') timestamp = timestamp.replace(/\-/g, '\/')
  } else {
    timestamp = new Date()
  }

  const date = new Date(timestamp)
  let yyyy = date.getFullYear() // 

  let MM = date.getMonth() + 1 // 
  MM = strpad(MM)

  let dd = date.getDate() // 
  dd = strpad(dd)

  let HH = date.getHours() // 
  HH = strpad(HH)

  let mm = date.getMinutes() // 
  mm = strpad(mm)

  let ss = date.getSeconds() // 
  ss = strpad(ss)

  if (formato.includes('yyyy')) time += `${yyyy}${interval}`
  if (formato.includes('MM')) time += `${MM}${interval}`
  if (formato.includes('dd')) time += `${dd}`
  if (formato.includes('HH')) time += ` ${HH}`
  if (formato.includes('mm')) time += `:${mm}`
  if (formato.includes('ss')) time += `:${ss}`

  return time
}

export default {
  getFormatDate,
  getTimeStamp,
  useTimestamp
}
