import jump from '@/router/jump.js'
import { initComponent } from '@/regisit/component.js'
import { sessionStorage } from '@/utils/StorageUtils.js'
import { SYSTEM_STATE } from '@/api/interface/enums.js'
import { default_locale } from '@/api/setting.js'

// 
export const filterPath = (code, next, store) => {
  // 
  const { languageType = '' } = store?.state?.base || {}
  store.commit('base/SET_LANGUAGE_TYPE', languageType ? languageType : default_locale)

  let path = ''
  if (code == SYSTEM_STATE.ISSTOP || code == SYSTEM_STATE.STOPNOTICE) path = '/stopNotice'
  if (code == SYSTEM_STATE.OUT) path = '/collapse'
  if (code == SYSTEM_STATE.IPLIMIT || code == SYSTEM_STATE.DEVICELIMIT) path = '/limit'
  if (code == SYSTEM_STATE.ACCOUNTLIMIT) path = '/login'

  if (next) {
    next(path)
  } else {
    window.$vm.$router.push(path)
  }
  if (document.getElementsByClassName('openAnimation')[0]) document.getElementsByClassName('openAnimation')[0].remove()
}

/**
 * 
 * notPage ==> Seo SEO 
 */
const notPageSeo = ['recomendar']
export default function (store, to, next) {
  jump().then(_ => {
    store.dispatch('common/setSiteList').then(_ =>
      store
        .dispatch('common/setInitialConfig')
        .then(_ =>
          store
            .dispatch('common/startUserData')
            .then(_ =>
              store.dispatch('common/getLanguageList').then(_ =>
                store
                  .dispatch('common/getLanguageInfo')
                  .then(_ => {
                    /**
                     * NOTE: URL
                     * TODO
                     * TODO
                     */
                    const isTourist = store.getters['base/isTourist']
                    const user = store.state.base.user

                    // 
                    let routerName = document.querySelectorAll('body')[0]
                    routerName =
                      routerName && routerName.getAttribute('route') ? routerName.getAttribute('route') : 'menu'

                    // 
                    if (isTourist && notPageSeo.includes(routerName)) routerName = 'menu'

                    // 
                    if (!isTourist && routerName == 'menu') {
                      const storageKey = 'first_guide_redirect_' + user.uid
                      const redirect = localStorage.getItem(storageKey)
                      if (redirect) routerName = redirect
                    }

                    // 
                    store.commit('base/SET_IS_FIRING', true)

                    // link_source
                    const { agentid, link_source = '' } = to.query || {}
                    sessionStorage.set('link_source', link_source)

                    // ,
                    store.dispatch('common/getInit', { name: routerName })

                    let query = {}
                    if (agentid) query.agentid = agentid != 'string' ? agentid[0] : agentid
                    if (link_source) query.link_source = link_source

                    next({ name: routerName, query })
                  })
                  .then(_ => {
                    // 
                    initComponent()
                  })
              )
            )
            .catch(code => {
              // 
              initComponent('part')
              // 
              filterPath(code, next, store)
            })
        )
        .catch(code => {
          // 
          initComponent('part')
          // 
          filterPath(code, next, store)
        })
    )
  })
}
