// 
const autoMove = function (el, changeAttr, endValue, name) {
  //   
  let currentValue
  const {
    // 
    offsetTop: eT,
    offsetLeft: eL
  } = el
  //  'left' 
  if (changeAttr === 'left') currentValue = eL
  //  'top' 
  if (changeAttr === 'top') currentValue = eT
  //    1 / 33
  const step = (endValue - currentValue) / 33
  // 
  if (el.__VueDragTimer__) clearInterval(el.__VueDragTimer__)
  // 
  el.__VueDragTimer__ = setInterval(() => {
    //   (js)
    if (Math.abs(endValue - currentValue) < Math.abs(step)) {
      el.style[changeAttr] = endValue + 'px'
      clearInterval(el.__VueDragTimer__)
      delete el.__VueDragTimer__
    } else {
      currentValue += step
      el.style[changeAttr] = currentValue + 'px'

      if (
        (name == 'left' && step >= 0) ||
        (name == 'right' && step <= 0) ||
        (name == 'top' && step >= 0) ||
        (name == 'bottom' && step <= 0)
      ) {
        clearInterval(el.__VueDragTimer__)
        delete el.__VueDragTimer__
      }
    }
  }, 5)
}

/**
 * 
 * 
 * 1
 * 2<=50
 * 3
 */
const autoStop = function (el) {
  const {
    // parentNode
    parentNode: { clientWidth: pW, clientHeight: pH, offsetTop: pT, offsetLeft: pL },
    // 
    offsetTop: eT,
    offsetLeft: eL,
    // (padding)
    offsetWidth: eW,
    offsetHeight: eH
  } = el
  /**
   * 
   * changeAttr:  'top'| 'left'
   * endValue: ()
   * distance
   * toggle: 
   */
  const stopConfigs = [
    {
      // 
      name: 'top',
      changeAttr: 'top',
      endValue: pT,
      distance: eT - pT,
      toggle: eT <= 50 + pT
    },
    {
      // 
      name: 'bottom',
      changeAttr: 'top',
      // endValue: pH + pT - eH,
      // distance: pH - eT - eH + pT,
      endValue: pT,
      distance: eT - pT,
      toggle: pH - eT - eH <= 50 - pT
    },
    {
      // 
      name: 'left',
      changeAttr: 'left',
      endValue: pL,
      distance: eL - pL,
      toggle: true
    },
    {
      // 
      name: 'right',
      changeAttr: 'left',
      endValue: pW + pL - eW,
      distance: pW - eL - eW + pL,
      toggle: true
    }
  ]
  // truedistancechangeAttrendValue
  const { changeAttr, endValue, name } = stopConfigs.filter(o => o.toggle).sort((a, b) => a.distance - b.distance)[0]
  autoMove(el, changeAttr, endValue, name)
}

export default autoStop
