/**
 * @desc: 
 * */
import { loadWebsocket } from '@/server/ws.js'
import { getEnvConfig } from '@/utils/collect.js'
import { SYSTEM_STATE } from '@/api/interface/enums.js'
import { lodash, datetime, cookieInfo } from '@/utils/helper.js'
import { ADS_TYPE, dynamicInitAds } from '@/server/ads/index'
import { getAreaCodes } from '@/api/modules/login.js'
import { gameDown, extGameActivity } from '@/api/modules/game.js'
import { channelBurialPoint, AJ_DOTTING, visitDotting } from '@/utils/dotting.js'
import {
  dynamic_lan_keys,
  default_locale,
  VUE_APP_REQUEST_ERROR,
  baseUrlTime,
  VUE_APP_TITLE,
  WS_HOST_SCHEME,
  VUE_APP_BACKUP_URL,
  VUE_APP_SCHEME,
  IS_MULTI_SITE_SHOW,
  PAGE_MULTI_SITE_SWITCH,
  SITE_FIRST_POPUP_BOOL
} from '@/api/setting.js'
import {
  getSiteList,
  commonConfig,
  getStopgsNotice,
  getBackupUrl,
  getLangInfo,
  getLangList,
  getNoticeList,
  getCarouselpopList,
  getActivitieSignList,
  getBannerList,
  getOutLinks,
  getRechargeInfoV2,
  getRechargeInfoV3,
  getDomainList
} from '@/api/modules/config.js'
import gameAPI from '@/utils/game/gameAPI.js'
import PublishSubscribe from 'publish-subscribe-js'

const state = {
  // API
  getApiBackupUrlNub: false,
  // app.vuekey
  updateAppKey: 0,
  // 
  isFirstInit: true,
  // 
  isFirstPopStetus: cookieInfo.getCookie(SITE_FIRST_POPUP_BOOL)
}
const getters = {
  getUpdateAppKey: state => state.updateAppKey,
  // 
  isMultiSiteShow: () => IS_MULTI_SITE_SHOW,
  //   1: 2: 3: --> [1,2,3] 
  pageMultiSiteSwitch: () => PAGE_MULTI_SITE_SWITCH
}
const mutations = {
  // API
  SET_GET_API_BACKUP_URL_NUB(state, payload) {
    state.getApiBackupUrlNub = payload
  },
  // app.vuekey
  SET_UPDATE_APP_KEY(state, payload) {
    state.updateAppKey = payload
  },
  // 
  SET_IS_FIRST_INIT(state, payload) {
    state.isFirstInit = payload
  },
  // 
  SET_IS_FIRST_POP_STATUS(state, payload) {
    cookieInfo.setCookie(SITE_FIRST_POPUP_BOOL, payload)
    state.isFirstPopStetus = payload
  }
}
const actions = {
  /**
   * 
   * 
   *    &&
   *     || &&
   * 	master 
   * 
   *    
   */
  setSiteList: async ({ state, getters, commit, dispatch, rootState, rootGetters }) => {
    if (!getters['isMultiSiteShow']) return Promise.resolve()

    const { languageType = '' } = rootState.base || {}
    const res = await getSiteList({ language: languageType }).catch(e => e)

    //  - 
    if (res == VUE_APP_REQUEST_ERROR) {
      // 
      if (getEnvConfig.index != getEnvConfig.urlLength) {
        return new Promise(resolve => {
          setTimeout(
            async () => {
              getEnvConfig.set()
              resolve(dispatch('setSiteList'))
            },
            baseUrlTime || 1 * 1000
          )
        })
      }
      // API
      if (!state.getApiBackupUrlNub && VUE_APP_BACKUP_URL) {
        commit('SET_GET_API_BACKUP_URL_NUB', true)
        await dispatch('getApiBackupUrl')

        return dispatch('setSiteList')
      }
    }
    // 
    if (res.code != 0) return Promise.resolve()

    /**
     * 
     * list 
     * default_site_id id
     */
    const { list = [], default_site_id } = res && res.data && lodash.isObject(res.data) ? res.data : {}
    // 
    const url = new URL(getEnvConfig.baseURL()) || {}
    const protocol = url.protocol || 'https:'
    const newList = list.map(item => {
      return {
        ...item,
        default_site_id,
        api_url: item.api_url.includes('http') ? item.api_url : `${protocol}//${item.api_url}`
      }
    })
    // 
    commit('base/SET_MULTI_CURRENCY_LIST', newList, { root: true })
    // 
    getEnvConfig.setSubSiteUrl(rootGetters['base/getCurSiteInfo'].api_url)

    return Promise.resolve()
  },
  // 
  setInitialConfig: async ({ commit, getters, dispatch, rootState, rootGetters }) => {
    const deviceid = rootGetters['base/getDeviceID']
    const { mainVer, subVer, pkgName, firstInstall, type, languageType, nativeVer, dataVersion } = rootState.base || {}
    let agentid = localStorage.getItem('agentid')
    const params = {
      mainVer,
      subVer,
      pkgName,
      agentid,
      firstInstall,
      type,
      language: languageType,
      nativeVer,
      dataVersion,
      deviceid
    }
    const res = await commonConfig(params).catch(e => e)

    //  &&  --> 
    if (!getters['isMultiSiteShow'] && res == VUE_APP_REQUEST_ERROR) {
      //  - 
      // 
      if (getEnvConfig.index != getEnvConfig.urlLength) {
        return new Promise(resolve => {
          setTimeout(
            async () => {
              getEnvConfig.set()
              resolve(dispatch('setInitialConfig'))
            },
            baseUrlTime || 1 * 1000
          )
        })
      }
      // API
      if (!state.getApiBackupUrlNub && VUE_APP_BACKUP_URL) {
        commit('SET_GET_API_BACKUP_URL_NUB', true)
        await dispatch('getApiBackupUrl')

        return dispatch('setInitialConfig')
      }
    }

    const resData = res && res.data && lodash.isObject(res.data) ? res.data : {}

    // 
    if (!Object.keys(resData).length) return Promise.reject(SYSTEM_STATE.OUT)

    commit('base/SET_SYSTEM_CONFIG', resData, { root: true })
    // 
    if (res.code == 1) return Promise.reject(SYSTEM_STATE.DEVICELIMIT)
    // IP
    if (res.code == 403) return Promise.reject(SYSTEM_STATE.IPLIMIT)
    // 
    if (resData.is_stops == 0) return Promise.reject(SYSTEM_STATE.ISSTOP)
    // 
    let stopNotice = {}
    if (resData.is_stopgs == 1) {
      stopNotice = await getStopgsNotice(params).catch(e => e)
      commit('base/SET_STOP_NOTICE', stopNotice.data, { root: true })
      return Promise.reject(SYSTEM_STATE.STOPNOTICE)
    }

    const title = document.getElementById('title')
    if (title) title.innerHTML = VUE_APP_TITLE

    // 
    const systemTimeDiff = datetime.dateDiff(new Date() / 1000, resData?.current_sys_time)
    // 
    commit('base/SET_SYSTEM_TIME_DIFF', systemTimeDiff, { root: true })
    // websocket
    commit('base/SET_WS_URL', `${WS_HOST_SCHEME}://${resData.gs_host}:${resData.gs_sport}`, { root: true })
    // 
    commit('switchState/SET_CURRENCY_SHOW', resData.currency_show == 1, { root: true })

    return Promise.resolve()
  },
  // IP
  startUserData: ({ commit, rootState, dispatch }) => {
    return new Promise((resolve, reject) => {
      const { user = {} } = rootState.base || {}
      const account = JSON.parse(JSON.stringify(user))
      // 
      const handle = (data = {}) => {
        if (data.code == 0) {
          // websocket
          loadWebsocket(window.$vm)
          resolve(data)
        }
        // ip 
        if (data.code == 403) reject(SYSTEM_STATE.IPLIMIT)
        // 
        if (data.code == 1) reject(SYSTEM_STATE.ACCOUNTLIMIT)
        // token 
        if (data.code == 1001) {
          // 
          commit('base/SET_USER_HISTORY', [], { root: true })
          // 
          commit('base/SET_USER', {}, { root: true })
          reject(SYSTEM_STATE.ACCOUNTLIMIT)
        }

        resolve(data)
      }

      if (!Object.keys(account).length) {
        dispatch(
          'base/createVisitorAccount',
          res => {
            handle(res)
          },
          { root: true }
        )
      } else {
        dispatch(
          'base/refreshAccount',
          {
            isInitial: true,
            callback: resData => {
              if (resData.code != 1001) {
                handle(resData)
              } else {
                // 
                commit('base/SET_USER_HISTORY', [], { root: true })
                // 
                commit('base/SET_USER', {}, { root: true })
                // 
                dispatch(
                  'base/createVisitorAccount',
                  res => {
                    handle(res)
                  },
                  { root: true }
                )
              }
            }
          },
          { root: true }
        )
      }
    })
  },
  // 
  getLanguageList: async ({ commit, rootState }) => {
    const { languageType = '' } = rootState.base || {}
    let setLangText = ''
    let langList = []

    const res = await getLangList().catch(e => e)
    if (res && res.code == 0) {
      langList = Array.isArray(res.data) ? res.data : []
      langList = langList.sort((a, b) => a.sort - b.sort)

      // 
      const langItem = langList.find(f => f.lang == languageType)
      if (langItem) {
        setLangText = langItem.lang
      } else {
        // 
        const defaultItem = langList.find(f => f.default == 1)
        // 
        const localLang = langList.find(f => f.lang == default_locale)

        setLangText = defaultItem
          ? defaultItem.lang
          : localLang
            ? localLang.lang
            : langList[0]
              ? langList[0].lang
              : default_locale
      }

      langList = langList.map(item => {
        const { lang = '' } = item
        const [first = ''] = lang ? lang.split('-') : []

        return {
          ...item,
          langText: first ? first.toUpperCase() : ''
        }
      })
    } else {
      setLangText = languageType ? languageType : default_locale
    }

    // 
    commit('base/SET_LANGUAGE_TYPE', setLangText, { root: true })
    commit('source/SET_LANGUAGE_LIST', langList, { root: true })
  },
  // key
  getLanguageInfo: async ({ commit, rootState }) => {
    const params = {
      name: dynamic_lan_keys.join(','),
      language: rootState.base.languageType
    }
    const res = await getLangInfo(params).catch(e => e)
    if (!res || res.code != 0) return

    const resData = res.data && Array.isArray(res.data) ? res.data : []
    commit('base/SET_LANG_MORE_INFO', resData, { root: true })
  },
  // 
  getActivityConfigFn: async ({ commit, dispatch, rootState }) => {
    const { languageType = '', user = {}, systemConfig = {} } = rootState.base || {}
    const { invite_new_has_gift_switch = 0 } = systemConfig

    if (invite_new_has_gift_switch == 1) {
      await dispatch('source/getActivityConfigFn', { language: languageType, token: user.token }, { root: true })
    } else {
      commit('source/SET_ACTIVITY_INVITE', null, { root: true })
    }
  },
  // 
  getGames: () => {
    return new Promise(resolve => {
      gameAPI.initGame(async (res = false) => {
        await gameAPI.getTabs()
        await gameAPI.getLobbyList()
        // res
        if (res == true) {
          setTimeout(() => {
            gameAPI.favorateUp()
          }, 100)
        }
        resolve(gameAPI)
      }, true)
    })
  },
  // 
  getNoticeList: async ({ commit, rootState }) => {
    const { languageType = '', user = {} } = rootState.base || {}
    const par = { language: languageType, token: user.token, type_h5: 1 }
    const res = await getNoticeList(par).catch(e => e)
    if (res.code != 0) return

    const notices = res.data && Array.isArray(res.data) ? res.data : []
    commit(
      'base/SET_NOTICES',
      notices.filter(item => item.is_popup == 1),
      { root: true }
    )
  },
  // 
  getCarouselpop: async ({ commit, rootState }) => {
    const { user: { token } = {}, languageType = {} } = rootState.base || {}
    const res = await getCarouselpopList({ token, language: languageType, notice_id: 0 }).catch(e => e)
    if (res.code != 0) return

    const list = res.data && Array.isArray(res.data) ? res.data : []
    commit('base/SET_CAROUSEL_LIST', list, { root: true })
  },
  // 
  getOtherData: async ({ dispatch, rootState, rootGetters }) => {
    const { user: { token = '' } = {}, languageType = {} } = rootState.base || {}
    const params = {
      version: 1,
      language: languageType,
      token: token
    }

    // 
    const isTourist = rootGetters['base/isTourist']
    // 
    if (!isTourist) await dispatch('base/showRedDotReceivedFun', params, { root: true })
  },
  // 
  getDotting: ({ dispatch, rootState }) => {
    const { user: { pixelid = '' } = {} } = rootState.base || {}

    // ads  ---> facebook pixelid
    if (pixelid) dynamicInitAds(ADS_TYPE.facebook, pixelid)
    // pixel ----> ,
    dispatch('base/getAjNoticeFun', {}, { root: true })
    // 
    channelBurialPoint(1)
    // AJ 
    AJ_DOTTING.init()
    // 
    visitDotting()
  },
  // 
  getGameDown: async ({ rootState }) => {
    const { systemConfig = {}, user = {}, languageType = '' } = rootState.base || {}

    //  0 1
    const isSport = systemConfig.ext_game_sport_show == 1
    //  - 
    const isSportView = window.location.hash == '#/sportView'
    if (isSportView && isSport) return

    await gameDown({ token: user.token, language: languageType }).catch(e => e)
  },
  //  v2
  getActivityInfoV2: async ({ rootState, commit }) => {
    const {
      user: { token = '' },
      languageType = ''
    } = rootState.base || {}
    const res = await getRechargeInfoV2({ language: languageType, token }).catch(e => e)
    if (!res || res.code != 0) return

    const resData = res.data && lodash.isObject(res.data) ? res.data : {}
    commit('source/SET_ACTIVITY', resData, { root: true })
  },
  //  v3
  getActivityInfoV3: async ({ rootState, commit }) => {
    const {
      user: { token = '' },
      languageType = ''
    } = rootState.base || {}
    const res = await getRechargeInfoV3({ language: languageType, token }).catch(e => e)
    if (!res || res.code != 0) return

    const resData = res.data && lodash.isObject(res.data) ? res.data : {}
    commit('source/SET_RECHARGE_INFO', resData, { root: true })
  },
  // 
  getFeatureInfo: async ({ rootState, rootGetters, dispatch }) => {
    const { user: { token } = {}, languageType = {} } = rootState.base

    // 
    const isTourist = rootGetters['base/isTourist']
    if (!isTourist) await dispatch('responsible/getFeatureInfo', { token, language: languageType }, { root: true })
  },
  // 
  getExtGameActivityList: async ({ rootState, commit }) => {
    const { user: { token } = {}, languageType = {} } = rootState.base
    const res = await extGameActivity({ token, language: languageType }).catch(e => e)
    if (!res || res.code != 0) return

    const list = Array.isArray(res.data) ? res.data : []
    commit('base/SET_GAME_ACTIVITY_LIST', list, { root: true })
  },
  // 
  getRegressionDialog: async ({ rootState, dispatch }) => {
    const { user: { token } = {}, languageType = {} } = rootState.base
    await dispatch('base/getRegressionDialog', { token, language: languageType }, { root: true }).catch(e => e)
  },
  // vip
  getVipSignList: async ({ commit, rootState, rootGetters }) => {
    const { user: { token } = {}, languageType = {}, systemConfig = {} } = rootState.base
    const { sign_switch = 0, sign_popup = 0 } = systemConfig
    if (sign_switch != 1 || sign_popup != 1 || rootGetters['base/isTourist']) return

    const params = { token: token, language: languageType }
    const res = await getActivitieSignList(params).catch(e => e)
    if (!res || res.code != 0) return

    const resData = res && res.data && lodash.isObject(res.data) ? res.data : {}
    commit('base/SET_VIP_SIGN_INFO', resData, { root: true })
  },
  // 
  getMenuBannerList: async ({ commit, rootState }) => {
    const { user: { token } = {}, languageType = {} } = rootState.base
    const res = await getBannerList({ type: 2, language: languageType, token }).catch(e => e)
    if (!res || res.code != 0) return

    const list = res.data && Array.isArray(res.data) ? res.data : []
    commit('source/SET_DESK_NOTICES', list, { root: true })
  },
  // 
  getAreaCodesList: async ({ commit, rootState }) => {
    const { user: { token } = {}, languageType = {} } = rootState.base
    const res = await getAreaCodes({ token, languageType }).catch(e => e)
    if (!res || res.code != 0) return

    const list = res.data && Array.isArray(res.data) ? res.data : []
    commit('source/SET_PHONE_PLACES', list, { root: true })
  },
  // 
  getOutLinkList: async ({ commit, rootState }) => {
    const { user: { token } = {}, languageType = {} } = rootState.base
    const res = await getOutLinks({ token, languageType }).catch(e => e)
    if (!res || res.code != 0) return

    const list = res.data && Array.isArray(res.data) ? res.data : []
    commit('base/SET_OUT_LINKS', list, { root: true })
  },
  // API
  getApiBackupUrl: async () => {
    const resUrl = await getBackupUrl(VUE_APP_BACKUP_URL).catch(e => e)
    // API
    if (Array.isArray(resUrl)) {
      let scheme = VUE_APP_SCHEME ? `${VUE_APP_SCHEME}://` : 'https://'
      let urlData = resUrl.map(v => {
        let url = v.includes('https://') || v.includes('http://') ? v : scheme + v
        return url
      })
      getEnvConfig.add(urlData)
    }
  },
  /**
   * NOTE: 
   * 1.
   * 2.
   * 3.
   * 4.
   * 5.
   * 6.vip
   * 7.
   * 8.
   * 9.
   * 10.
   * 11.
   * 12.
   * 13.
   * 14. v2
   * 15. v3
   * 16.
   *
   * 1,2,3,4,5,6 ==> 
   */
  getCommonInterface({ dispatch }, callback) {
    // 
    Promise.all([
      dispatch('getActivityConfigFn'),
      dispatch('getNoticeList'),
      dispatch('getCarouselpop'),
      dispatch('getFeatureInfo'),
      dispatch('getRegressionDialog'),
      dispatch('getVipSignList')
    ]).then(_ => {
      if (callback && typeof callback == 'function') callback()
    })

    dispatch('getGameDown')
    dispatch('getMenuBannerList')
    dispatch('getOtherData')
    dispatch('getGames')
    dispatch('getAreaCodesList')
    dispatch('getOutLinkList')
    dispatch('getExtGameActivityList')
    dispatch('getActivityInfoV2')
    dispatch('getActivityInfoV3')
    dispatch('getDotting')
  },
  // 
  getInitDialog({ rootState }, name) {
    const { DUALOG_MANAGER_CLEAR = 'dualog_manager_clear', DUALOG_MANAGER_KEY = 'dualog_manager_key' } =
      rootState.dialog
    // 
    PublishSubscribe.publish(DUALOG_MANAGER_CLEAR)
    setTimeout(() => {
      PublishSubscribe.publish(DUALOG_MANAGER_KEY, name)
    }, 67)
  },
  // 
  getInit({ state, getters, commit, dispatch, rootGetters }, { name, isTourist = true }) {
    /**
     * 
     * 
     */
    // 
    if (
      state.isFirstInit &&
      getters['isMultiSiteShow'] &&
      !state.isFirstPopStetus &&
      getters['pageMultiSiteSwitch'].includes(1)
    ) {
      window.$pop
        .screenSite({
          submit: (item = {}) => {
            commit('SET_IS_FIRST_INIT', false)
            commit('SET_IS_FIRST_POP_STATUS', true)
            // 
            const currSite = JSON.parse(JSON.stringify(rootGetters['base/getCurSiteInfo']))
            // 
            if (currSite.id == item.id) {
              // 
              commit('base/SET_MULTI_CURRENCY_INFO', { id: item.id, master: item.master }, { root: true })
              dispatch('getCommonInterface', () => {
                dispatch('getInitDialog', name)
              })
            } else {
              dispatch('base/changeSite', item, { root: true })
            }
          }
        })
        .show()
    } else {
      dispatch('getCommonInterface', () => {
        if (isTourist) dispatch('getInitDialog', name)
      })
    }
  }
  /**
   * @desc 
   * 1. domain_report_switch==1
   * 2. 
   * 3. 
   */
  // getDomainVerify: async ({ rootState }) => {
  //   const { systemConfig = {}, languageType = '' } = rootState.base || {};
  //   if (systemConfig.domain_report_switch != 1) return;

  //   const res = await getDomainList({ language: languageType }).catch(e => e)
  //   if (res.code != 0) return
  // }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
