/**
 * @desc: 
 * */
import { make } from 'vuex-pathify'
import {
  getFeatureContent,
  setReminderTime,
  setLimitTime,
  setDayPlayTime,
  setLossLimit
} from '@/api/modules/responsible.js'
import { lodash } from '@/utils/helper.js'
import { respTimeReminder, respLossLimit, respDailyPlay } from '@/utils/responsible.js'
import globalStore from '@/store'
import ROUTES from '@/router/routes'

const state = {
  responsibleSwitch: 0, //  1 0
  headContent: '', // 
  agreementUrl: '', // 
  remindItem: {
    // 
    content: '', // 
    status: 0, //  0 1
    config: [], // 
    edit_time: 0, //  cd 
    edit_cd_time: 0, //  cd 
    reminder_total_time: 0, // 
    reminder_time: 0 // 
  },
  limitItem: {
    // 
    content: '', // 
    status: 0, //  0 1
    config: [], // 
    edit_cd_time: 0, //  cd 
    edit_time: 0, //  cd 
    apply_disabled: null // , apply_disabled.disabled_desc
  },
  lossLimitItem: {
    content: '', // 
    status: 0, //  0 1
    edit_time: 0, // 
    edit_cd_time: 0, // 
    loss_limit: {
      day_limit: 0, //
      week_limit: 0, //
      month_limit: 0 //
    },
    is_loss_limit: 0 // 0  1 23user_statusis_limit
  },
  dayPlayItem: {
    content: '', // 
    status: 0, //  0 1
    edit_time: 0, // 
    edit_cd_time: 0, // 
    is_day_play_time: 1, // 10
    day_play_time: {
      start_hour: '', // 
      start_min: '', // 
      end_hour: '', // 
      end_min: '' // 
    },
    startTime: 0, // 
    endTime: 0 // 
  },
  remindDialogShow: false // 
}

const getters = {}
const mutations = {
  ...make.mutations(state),
  // 
  SET_REMIND_STATUS(state, status) {
    state.remindItem.status = status
  },
  // 
  SET_REMINDER_RESIDUE_TIME(state, num) {
    state.remindItem.reminder_total_time = num
  },
  // 
  SET_REMIND_TIME(state, num) {
    state.remindItem.reminder_time = num
  },
  // 
  SET_LIMIT_STATUS(state, status) {
    state.limitItem.status = status
  },
  // cd
  SET_REMIND_CD_TIMER(state, num) {
    state.remindItem.edit_cd_time = num
  },
  // 
  SET_LOSS_LIMIT_STATUS(state, status) {
    state.lossLimitItem.is_loss_limit = status
  },
  // cd
  SET_LOSS_CD_TIMER(state, num) {
    state.lossLimitItem.edit_cd_time = num
  },
  // cd
  SET_Day_Play_CD_TIMER(state, num) {
    state.dayPlayItem.edit_cd_time = num
  }
}
const actions = {
  // 
  getFeatureInfo: async ({ state, commit, dispatch }, params) => {
    const { callback, ...restPar } = params
    const res = await getFeatureContent(restPar).catch(e => e)
    if (!res || res.code != 0) return

    const resData = res.data && lodash.isObject(res.data) ? res.data : {}
    const { agreement_url = '', head_content = '', responsible_switch = 0, body = {} } = resData
    const { interval_reminder = {}, login_limit = {}, day_play_time = {}, loss_limit = {} } = body
    const { status: reminderStatus = 0 } = interval_reminder
    const { status: limitStatus = 0 } = login_limit
    const { status: dayPlayStatus = 0 } = day_play_time
    const { status: lossLimitStatus = 0 } = loss_limit

    commit('SET_RESPONSIBLE_SWITCH', responsible_switch)
    commit('SET_HEAD_CONTENT', head_content)
    commit('SET_AGREEMENT_URL', agreement_url)
    commit('SET_REMIND_ITEM', Object.assign({}, state.remindItem, interval_reminder))
    commit('SET_REMINDER_RESIDUE_TIME', interval_reminder.reminder_time)
    commit('SET_LIMIT_ITEM', Object.assign({}, state.limitItem, login_limit))

    commit('SET_LOSS_LIMIT_ITEM', Object.assign({}, state.lossLimitItem, loss_limit))

    if (responsible_switch && day_play_time.is_day_play_time == 0) {
      dispatch('disabledPlayLogout')
    }
    const { start_hour = '', start_min = '', end_hour = '', end_min = '' } = day_play_time?.day_play_time || {}
    if (start_hour && start_min && end_hour && end_min) {
      const now = new Date()
      now.setHours(parseInt(start_hour))
      now.setMinutes(parseInt(start_min))
      now.setSeconds(0)
      day_play_time.startTime = now.getTime()
      now.setHours(parseInt(end_hour))
      now.setMinutes(parseInt(end_min))
      now.setSeconds(59) // 
      day_play_time.endTime = now.getTime()
    }
    commit('SET_DAY_PLAY_ITEM', Object.assign({}, state.dayPlayItem, day_play_time))

    // 
    if (responsible_switch != 1 || reminderStatus != 1) {
      //  
      $pop.remindAlarm().hide()
      //  
      $pop.responRemind().hide()

      commit('SET_REMIND_DIALOG_SHOW', false)
    }
    // 
    if (responsible_switch != 1 || limitStatus != 1) {
      // 
      $pop.responLimit().hide()
    }
    // 
    if (responsible_switch != 1 || lossLimitStatus != 1) {
      // 
      $pop.responLossLimit().hide()
    }
    // 
    if (responsible_switch != 1 || dayPlayStatus != 1) {
      // 
      $pop.responDailyPlay().hide()
    }

    // 
    dispatch('startCountDown')

    // cd0 
    if (responsible_switch && loss_limit.edit_cd_time) {
      dispatch('startLossCountDown')
    }

    if (responsible_switch && (day_play_time.edit_cd_time || day_play_time.startTime)) {
      // cd0 
      day_play_time.edit_cd_time && dispatch('startDayPlayCountDown')
      //  
      day_play_time.endTime && day_play_time.startTime && dispatch('watchDayPlayTime')
    }
    if (params.callback) params.callback()
  },
  // ws 
  getWsFeatureInfo: async ({ state, commit, dispatch }, params) => {
    const { user = {}, languageType = '' } = globalStore.state.base || {}

    dispatch('getFeatureInfo', {
      token: user.token,
      language: languageType,
      callback: () => {
        dispatch('renewRemindStorage', {
          sign: 'wsInit',
          data: user,
          callback: () => {
            // 
            dispatch('startCountDown')
          }
        })
      }
    })
  },
  // 
  setReminderTime: ({ state, commit, dispatch }, params) => {
    return new Promise(async (resolve, reject) => {
      const res = await setReminderTime(params).catch(e => e)
      if (!res || res.code != 0) return reject({ code: -1, msg: res && res.msg })

      const resData = res.data && lodash.isObject(res.data) ? res.data : {}
      // edit_cd_time 
      // params.time 
      const { edit_cd_time = 0 } = resData
      commit('SET_REMIND_ITEM', Object.assign({}, state.remindItem, { edit_cd_time, reminder_time: params.time }))
      commit('SET_REMINDER_RESIDUE_TIME', params.time)

      const user = globalStore.state.base.user
      // 
      dispatch('renewRemindStorage', {
        sign: 'dialog',
        data: user,
        callback: () => {
          // 
          dispatch('startCountDown')
        }
      })

      resolve({ code: 0, msg: res.msg })
    })
  },
  // 
  deleteReminderTime: ({ state, commit, dispatch }, params) => {
    return new Promise(async (resolve, reject) => {
      const res = await setReminderTime(params).catch(e => e)
      if (!res || res.code != 0) return reject({ code: -1, msg: res && res.msg })

      commit(
        'SET_REMIND_ITEM',
        Object.assign({}, state.remindItem, { edit_cd_time: 0, reminder_total_time: 0, reminder_time: 0 })
      )

      const user = globalStore.state.base.user
      // 
      dispatch('renewRemindStorage', {
        sign: 'dialog',
        data: user,
        callback: () => {
          // 
          dispatch('startCountDown')
        }
      })

      resolve({ code: 0, msg: res.msg })
    })
  },
  /**
   * 
   * @param {*} sign  login:logout:, dialog:wsInit:
   * @param {*} data 
   * @param {*} callback 
   */
  renewRemindStorage({ state }, { sign = '', data = {}, callback }) {
    const { uid = '' } = data
    const { responsibleSwitch = 0, remindItem = {} } = state
    const { status = 0, reminder_time = 0, reminder_total_time = 0 } = remindItem

    // 
    // remindDialogTime: { id: { lastLogoutTime: (), currentLoginTime: (), firstDialogTime: () } }
    // firstDialogTime > 0:   = -1
    let remindDialogTime = localStorage.getItem('REMIND_DIALOG_TIME') || '{}'
    remindDialogTime = JSON.parse(remindDialogTime)
    if (!remindDialogTime[uid]) remindDialogTime[uid] = {}

    switch (sign) {
      case 'login':
        remindDialogTime[uid].currentLoginTime = parseInt(new Date().getTime() / 1000)

        // 
        //  55
        const lastTime = remindDialogTime[uid].lastLogoutTime || 0
        const currentTime = remindDialogTime[uid].currentLoginTime || 0

        if (currentTime - lastTime >= 300) {
          remindDialogTime[uid].firstDialogTime = reminder_total_time
            ? parseInt(new Date().getTime() / 1000) + reminder_total_time
            : -1
        }
        break
      case 'logout':
        remindDialogTime[uid].lastLogoutTime = parseInt(new Date().getTime() / 1000)
        break
      case 'dialog':
        remindDialogTime[uid].firstDialogTime = reminder_total_time
          ? parseInt(new Date().getTime() / 1000) + reminder_total_time
          : -1
        break
      case 'wsInit':
        if (responsibleSwitch != 1 || status != 1) remindDialogTime[uid] = {}
        break
    }

    localStorage.setItem('REMIND_DIALOG_TIME', JSON.stringify(remindDialogTime))

    if (callback) callback()
  },
  //  
  startCountDown: ({ state, dispatch }) => {
    // 
    respTimeReminder.init(() => {
      // 
      respTimeReminder.getReminderTime()
      respTimeReminder.getReminderEditCd()
    })
  },
  // 
  setLimitLogin: ({ state, commit }, params) => {
    return new Promise(async (resolve, reject) => {
      const res = await setLimitTime(params).catch(e => e)
      if (!res || res.code != 0) return reject({ code: -1, msg: res && res.msg })

      const resData = res.data && lodash.isObject(res.data) ? res.data : {}
      // edit_cd_time 
      const { edit_cd_time = 0 } = resData
      commit('SET_LIMIT_ITEM', Object.assign({}, state.limitItem, { edit_cd_time }))

      resolve({ code: 0, msg: res.msg })
    })
  },
  // 
  setLoss: ({ state, commit, dispatch }, params) => {
    return new Promise(async (resolve, reject) => {
      const res = await setLossLimit(params).catch(e => e)
      if (!res || res.code != 0) return reject({ code: -1, msg: res && res.msg })
      const resData = res.data && lodash.isObject(res.data) ? res.data : {}
      const { edit_cd_time = 0, loss_limit = null } = resData
      commit('SET_LOSS_LIMIT_ITEM', Object.assign({}, state.lossLimitItem, { edit_cd_time, loss_limit }))

      // cd0 
      const { responsibleSwitch = 0 } = state
      if (responsibleSwitch && edit_cd_time) {
        dispatch('startLossCountDown')
      }
      resolve({ code: 0, msg: res.msg })
    })
  },
  startLossCountDown: ({ state, dispatch }) => {
    respLossLimit.init(() => {
      respLossLimit.getLossLimitEditCd()
    })
  },
  // 
  setDayPlay: ({ state, commit, dispatch }, params) => {
    return new Promise(async (resolve, reject) => {
      const res = await setDayPlayTime(params).catch(e => e)
      if (!res || res.code != 0) return reject({ code: -1, msg: res && res.msg })
      const resData = res.data && lodash.isObject(res.data) ? res.data : {}
      const { edit_cd_time = 0, day_play_time = null } = resData
      const { start_hour = '', start_min = '', end_hour = '', end_min = '' } = day_play_time || {}
      let endTime = 0
      let startTime = 0
      if (start_hour && start_min && end_hour && end_min) {
        const now = new Date()
        now.setHours(parseInt(start_hour))
        now.setMinutes(parseInt(start_min))
        now.setSeconds(0)
        startTime = now.getTime()
        now.setHours(parseInt(end_hour))
        now.setMinutes(parseInt(end_min))
        now.setSeconds(59) // 
        endTime = now.getTime()
      }
      commit(
        'SET_DAY_PLAY_ITEM',
        Object.assign({}, state.dayPlayItem, { edit_cd_time, day_play_time, startTime, endTime })
      )

      const { responsibleSwitch = 0 } = state
      if (responsibleSwitch && (edit_cd_time || startTime || endTime)) {
        // cd0 
        edit_cd_time && dispatch('startDayPlayCountDown')
        //  
        endTime && startTime && dispatch('watchDayPlayTime')
      }
      resolve({ code: 0, msg: res.msg })
    })
  },
  // 
  startDayPlayCountDown: ({ state, dispatch }) => {
    respDailyPlay.init(() => {
      respDailyPlay.getDayPlayEditCd()
    })
  },
  // 
  watchDayPlayTime: ({ state, dispatch }) => {
    respDailyPlay.init(() => {
      respDailyPlay.hasDayPlayTime()
    })
  },
  // 
  disabledPlayLogout: async ({ state, dispatch }) => {
    await globalStore.dispatch('base/logout')
    if (window.globalRouter) window.globalRouter.push(ROUTES[0].path)
    dispatch('modal/setLoginRegistShow', {}, { root: true })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
