/**
 *
 * game_list:

    gidid
    name_en
    name_other:
    least_gold
    game_type0 1
    style
    back_pic
    position_set
    entrance_type0 1
    mtc_st_date
    mtc_end_date
    mtc_status
    pic_phone_url2,3fury 
    pic_pc_url2,3fury pc
    pic_urlury(1)
    small_pic_url
    show_rate_tips1 0
    egc_rate
    way_list
    sub_game_list
        gameidid
        name
        gcodecode
        tab
        show_type 10
        thumb_url
        thumb_urlFury
        mtc_status
        mtc_msg
        mtc_st_date
        mtc_end_date
 *
 *
 */

/**
 * game_type

    idid
    type_name
    factory_is_show0 1
    has_child0 1
    show_game_num
    has_child_list: 
        idid
        type_name
        pidid
        pc_iconpc
        iphone_icon
        label0 1NEW 2HOT
        style
        is_favorite

    game_list
        gidid
        gameidid
        entrance_type0 1



    game_lobby_type: 

        id:id
        type_name
        icon
        icon_no
        category_type1  2  3


    other_game_factory
 *
 *
 */

/**
 * 
 */
/**
    finSubCategory 
    getChildListInfo  
    getMainList  
    findGameFactory 
*/
const utils = {
  /**
   * @desc 
   * @step gidsub_gidfreePlay: true,false
   *
   * 
   */
  setGameFreePlayAuth(params, list) {
    let idx = -1
    if (params.gid) {
      idx = list.findIndex(v => v.gid == params.gid)
    }
    if (params.gameid && params.gameid != params.gid) {
      idx = list.findIndex(v => v.gid == params.gid && v.sub_gids.includes(params.gameid))
    }

    if (idx == -1) params.freePlay = false
    if (idx > -1) params.freePlay = true
    return params
  },

  // gidgameId 
  /*
        entrance_type  == 0  0 1
        sub_game_list
        gamelist
        pic_pc_urlpic_phone_urlwebpic_url
         thumb_urlpic_url
        //  1 pic_url 
        factoryName 
        gameListInfo 
        gameInfo 
        pictureUrl 
        show_rate_tips  egc
        egc_rate egc
        isFully 
		sub_platform_name_config  
    */
  getGameListInfo(gameListInfo, gameInfo, pictureUrl, languageType) {
    const { entrance_type = 0 } = gameInfo
    // entrance_type  == 0  0 1
    if (entrance_type == 0) {
      //  
      // style: 1 pic_url app pc
      // style: 23 pic_phone_urlpic_pc_url app pc
      const isFully = [2, 3].includes(gameListInfo[gameInfo.gid]?.style)
      let gameInfoData = {
        ...gameListInfo[gameInfo.gid],
        pic_url: pictureUrl + gameListInfo[gameInfo.gid]?.pic_url,
        pic_pc_url: pictureUrl + gameListInfo[gameInfo.gid]?.pic_pc_url,
        pic_phone_url: pictureUrl + gameListInfo[gameInfo.gid]?.pic_phone_url,
        small_pic_url: pictureUrl + gameListInfo[gameInfo.gid]?.small_pic_url,
        gameName: gameListInfo[gameInfo.gid]?.name_en,
        factoryName: '',
        isFully
      }
      return gameInfoData
    } else {
      // entrance_type == 1 gidsub_game_list 
      //show_rate_tips egc_ratefactoryName sub_game_list
      let gameData = gameListInfo[gameInfo.gid] || {}
      const {
        mtc_end_date = 0,
        mtc_end_time = 0,
        mtc_msg = '',
        mtc_st_date = 0,
        mtc_start_time = 0,
        mtc_status = 0,
        sub_game_list = {},
        open_mode = 0,
        name_en = '',
        name_other = '',
        show_rate_tips,
        egc_rate,
        sub_platform_name_config = {}
      } = gameData
      // 
      const factoryName = languageType != 'en-us' && name_other ? name_other : name_en

      let gameInfoData = {
        thumb_url: pictureUrl + sub_game_list[gameInfo.gameid]?.thumb_url,
        pic_url: pictureUrl + sub_game_list[gameInfo.gameid]?.thumb_url,
        gameName: sub_game_list[gameInfo.gameid]?.name_en,
        factoryName,
        show_rate_tips,
        egc_rate,
        ...sub_game_list[gameInfo.gameid],
        mtc_end_date,
        mtc_end_time,
        mtc_msg,
        mtc_st_date,
        mtc_start_time,
        mtc_status,
        open_mode, //  0: 1:(wickets9)
        // isFully: sub_game_list[gameInfo.gameid]?.show_type != 1, // 10
        isFully: false, //
        sub_platform_name_config
      }
      return gameInfoData
    }
  },
  // 
  getChildListInfo(gameListInfo, params, item, pictureUrl, languageType) {
    // isFully 
    const isFully = item.style == 2 || item.style == 3 ? true : false

    return {
      ...item,
      pic_phone_url: `${pictureUrl}${isFully ? item?.pic_phone_url : item?.iphone_icon}`,
      pic_pc_url: `${pictureUrl}${isFully ? item?.pic_pc_url : item?.pc_icon}`,
      gameName: item?.type_name_en,
      has_child: params.has_child,
      isFully,
      game_list: params.game_list[item.id]
        ? params.game_list[item.id].map(v => {
            // params.game_list[item.id]
            return {
              ...v,
              ...utils.getGameListInfo(gameListInfo, v, pictureUrl, languageType),
              has_child: 0,
              game_sub_id: v.id // id
            }
          })
        : []
    }
  },
  // 
  findGameFactoryChild(gameListInfo, params, item, pictureUrl, languageType) {
    const gameItem = gameListInfo[item.gid]
    gameItem.gameid = item.sub_gid
    return { has_child: params.has_child, ...utils.getGameListInfo(gameListInfo, gameItem, pictureUrl, languageType) }
  }
}

class GameList {
  /**
   * @props(type) 
   * @props(gamelist) 
   * @props(gameListInfo) 
   * @props(other_game) 
   * @props(logos) 
   * @props(defaultGameTab) 
   * @props(gameFactory) 
   * @props(experience) 
   * @props(gameArr) 
   * @props(allSpecials) 
   * @props(favorates) 
   * @props(recents) 
   */
  constructor(games, logos, gameArr, pictureUrl, languageType) {
    this.types = []
    this.gamelist = games.game_type || []
    this.gameListInfo = games.game_list || []
    this.otherGames = games.other_game || []
    this.logos = logos
    const gameLobbyType =
      games && games.game_lobby_type && Array.isArray(games.game_lobby_type) ? games.game_lobby_type : []
    this.defaultGameTab = gameLobbyType.map((tab, i) => ({
      ...tab,
      icon: pictureUrl + tab.icon,
      icon_on: pictureUrl + tab.icon_on
    }))
    this.gameFactory = games.other_game_factory
    this.experience = games.experience_list
    this.gameArr = gameArr
    this.pictureUrl = pictureUrl
    this.allSpecials = []
    this.favorates = []
    this.recents = []
    this.languageType = languageType
  }
  /**
   * @desc 
   */
  getTypes = () => {
    if (!this.gamelist) return []
    let tabs = this.gamelist.map(({ id, type_name, icon, icon_on, has_child, factory_is_show, show_game_num }) => ({
      id,
      type_name,
      icon,
      icon_on,
      has_child,
      factory_is_show,
      show_game_num
    }))
    return this.defaultGameTab.concat(tabs)
  }
  //
  setFavorate = list => {
    this.favorates = list
  }
  //
  setRecennt = list => {
    this.recents = list
  }
  /**
   * 
   *  has_child: 0 1 0 game_list; 1 has_child_list
   * factory_is_show:  0: 1: 
   * show_game_num
   */
  findMainList = () => {
    let l = []
    if (!this.gamelist) this.gamelist = []
    this.gamelist.forEach(item => {
      let t = Object.assign({}, item, { ckeys: `id_${Math.random().toFixed(5)}_${new Date().getTime()}` })
      t.game_list = this.getGameList(item)
      t = { ...t, ...this.getLobbyTypeIconList(item) }
      l.push(t)
    })
    this.allSpecials = l
    return l
  }
  /**
   * @desc 
   *
   */
  findSubGamelist = (params = {}) => {
    let list = []
    params &&
      params.game_list.forEach(item => {
        if (item.sub_game_list) {
          list = list.concat(item.sub_game_list)
        } else {
          list.push(item)
        }
      })
    return list
  }
  /**
   * 
   *  :
   *      1. 
   *      2.        localstorage
   *      3.               localstorage
   *  
   *      1.  Lobby
   *      2. favorate
   */
  findSpecialGame = id => {
    let t = this.defaultGameTab.findIndex(tab => tab.id == id)
    if (t == 0) return this.findMainList()
    if (t == 1) return this.favorates
    let findVal = this.findMainList().find(item => item.id == id)
    return !findVal ? [] : findVal.game_list
  }
  // has_child == 0 0 1 game_list
  getGameList = (params = {}) => {
    if (params.has_child == 0) {
      // getGameListInfo 
      const gameList = params.game_list[0] || []
      let gameListData = gameList.map(item => {
        return {
          ...item,
          has_child: 0,
          ...utils.getGameListInfo(this.gameListInfo, item, this.pictureUrl, this.languageType)
        }
      })
      return gameListData
    } else {
      let gameListData = params.has_child_list.map(item => {
        return {
          ...utils.getChildListInfo(this.gameListInfo, params, item, this.pictureUrl, this.languageType),
          has_child: 1
        }
      })
      return gameListData
    }
  }

  /**
   * 
   * @param {data} data {list} list 
   * @returns
   */
  favorateUp = (data = {}) => {
    const { list = [] } = data
    let gameList = []

    list.forEach(vItem => {
      let game = vItem
      // has_child_list
      if (game.has_child == 1) {
        let gameFileter = this.gamelist.some(item => item.has_child_list.some(v => v.id == game.id))
        if (gameFileter) gameList.push(game)
      }
      // 
      if (game.has_child == 0 && game.entrance_type == 1) {
        const gameFileter = this.gamelist.some(item => {
          // has_child_list 
          if (item.has_child_list.length > 0) {
            return (
              item.game_list[game.sub_type_id] &&
              item.game_list[game.sub_type_id].some(v => v.gameid == game.gameid && v.gid == game.gid)
            )
          } else {
            // has_child_list game_list 
            return (
              Array.isArray(item.game_list) &&
              item.game_list.length &&
              item.game_list.some(vGame => vGame && vGame.some(v => v.gameid == game.gameid && v.gid == game.gid))
            )
          }
        })
        if (gameFileter) gameList.push(game)
      }
    })
    return gameList
  }
  /**
   * 
   * @param {Object} data {id, sub_id}
   * @returns
   */
  finSubCategory = (data = {}) => {
    const { id, sub_id } = data
    const category = this.gamelist.find(item => item.id == id)
    if (!category) return null
    const { has_child = 0, has_child_list = [] } = category
    if (has_child == 1) {
      const mainGame = has_child_list.find(item => item.id == sub_id)
      return utils.getChildListInfo(this.gameListInfo, category, mainGame, this.pictureUrl, this.languageType)
    }
    return {}
  }
  /**
   * 
   * @param {Object} data {id}
   * @returns
   */
  getLobbyTypeIconList = (data = {}) => {
    const gameItem = this.defaultGameTab.filter(item => item.id == data.id)
    if (gameItem.length > 0) return gameItem[0]
    return {}
  }
  /**
   * 
   * @param {Object} data {id}
   * @returns
   */
  findGameFactory = (data = {}) => {
    const { id = '' } = data
    const category = this.gameFactory.find(item => item.id == id)
    if (!category) return []
    const { game = [] } = category
    const gameList = game.map(item => {
      return {
        ...item,
        ...utils.findGameFactoryChild(this.gameListInfo, category, item, this.pictureUrl, this.languageType)
      }
    })
    return gameList
  }
}

module.exports = GameList
