import Vue from 'vue'
import _color from '@style/_global/_color.module.scss'

let limit_zindex = Number(_color.currency_wrapper_box_zindex) // 
let custom_zindex = Number(_color.custom_dialog_box_zindex) // 
let perform_zindex = Number(_color.custom_game_perform_zindex) // 
let max_zindex = 100010 // 

/**
 * page: limit:, custom:, perform:vip, max:/
 */
const html = document.querySelectorAll('html')[0]
Vue.directive('zinx', {
  // 
  bind(el, { value = {} }) {
    if (!el) return

    const { page = 'custom', isScroll = true } = value
    // html
    if (html && isScroll) html.style.overflowY = 'hidden'

    const [mask, ...rest] = el.children || []
    if (mask) {
      switch (page) {
        // 
        case 'custom':
          custom_zindex++
          mask.style.zIndex = custom_zindex
          break
        // 
        case 'limit':
          limit_zindex++
          mask.style.zIndex = limit_zindex
          break
        // vip
        case 'perform':
          perform_zindex++
          mask.style.zIndex = perform_zindex
          break
        // 
        case 'max':
          max_zindex++
          mask.style.zIndex = max_zindex
          break

        default:
          mask.style.zIndex = 1500
          break
      }
    }
    if (rest.length > 0) {
      switch (page) {
        // 
        case 'custom':
          custom_zindex++
          rest.forEach(dom => (dom.style.zIndex = custom_zindex))
          break
        // 
        case 'limit':
          limit_zindex++
          rest.forEach(dom => (dom.style.zIndex = limit_zindex))
          break
        // vip
        case 'perform':
          perform_zindex++
          rest.forEach(dom => (dom.style.zIndex = perform_zindex))
          break
        // 
        case 'max':
          max_zindex++
          rest.forEach(dom => (dom.style.zIndex = max_zindex))
          break

        default:
          mask.style.zIndex = 1500
          break
      }
    }
  },
  // 
  unbind(el, { value = {} }) {
    if (!el) return

    const { isScroll = true } = value
    // html
    if (html && isScroll) html.style.overflowY = 'unset'
  }
})
