/**
 * @desc: 
 * */
import { make } from 'vuex-pathify'
import {
  getRechargeGold,
  getAjNotice,
  getWithdrawGold,
  getFirstChargeNum,
  showRedDotReceived,
  readDotStatus
} from '@/api/modules/config.js'
import { visitorLogin, getUserInfo } from '@/api/modules/login'
import { getPlatform, createDeviceId, Storage, lodash, multiSite } from '@/utils/helper.js'
import { getRegressionDialog } from '@/api/modules/regression.js'
import { AdjustlogEvent } from '@/utils/helper'
import { EVENT_KEY, logEvent, getFacebookPixelId } from '@/server/ads/index'
import { getEnvConfig } from '@/utils/collect.js'
import { AJ_DOTTING } from '@/utils/dotting.js'
import { filterPath } from '@/router/init.js'
import i18n from '@/utils/i18n'

const state = {
  isApp: true,
  languageType: '',
  // 
  mainVer: 1,
  subVer: 4,
  pkgName: 'h5_client', // 
  nativeVer: 0, // 
  platform: getPlatform(),
  deviceid: createDeviceId(),
  firstInstall: false,
  type: 101,
  dataVersion: 0,
  //
  systemConfig: {},

  // 
  user: Storage.get('user'), //
  // 
  visitor: Storage.get('visitor'),
  userHistory: [], //
  userDataVersion: 0, //

  dialogInfo: {}, // 

  //
  // 
  volume: 0,
  activityDetail: {},
  showRedDot: {}, // 

  isFiring: false, // 
  wsUrl: '', // websocket
  isRotate: false, // 
  isForced: false, // 
  notices: [], //
  carouselList: [], // 
  stopNotice: {}, //
  outLinks: [], //drawer-pannel
  isForceLogout: false,
  adJustKey: null,

  KfNoticeData: {
    // 
    question_id: 0, // id
    waiting: 0, // 
    processing: 0, // 
    processed: 0 // 
  },
  apkActivityGold: {
    //  
    apk_send_gold: 0, // 
    apk_show_switch: 0 // 
  },
  systemTimeDiff: 0, // 
  gameActivityList: [], // 
  isRefreshUser: true,
  langMoreInfo: [], // 
  reportLastTime: {}, // 
  vipSignInfo: {}, // vip
  multiCurrencyList: [], // /
  multiCurrencyInfo: multiSite.get() // /
}

const getters = {
  getAdJustKey(state) {
    return state.adJustKey
  },
  getDeviceID(state) {
    if (state.deviceid != '') return state.deviceid
    return createDeviceId()
  },
  getRootParams(state) {
    return {
      mainVer: state.mainVer,
      subVer: state.subVer,
      pkgName: state.pkgName,
      platform: state.platform,
      deviceid: state.deviceid,
      agentid: state.agentid,
      firstInstall: state.firstInstall,
      type: state.type,
      dataVersion: state.dataVersion,
      nativeVer: state.nativeVer,
      language: state.languageType
    }
  },
  // 
  loginSwitch(state) {
    const { login_weight_conf } = state.systemConfig
    return login_weight_conf?.some(v => v.status == 1)
  },
  // 
  regSwitch(state) {
    const { reg_login_weight_conf } = state.systemConfig
    return reg_login_weight_conf?.some(v => v.status == 1)
  },
  // 
  chatPromoteSwitch(state) {
    return parseInt(state.systemConfig.ai_promote_robot_switch) == 1
  },
  // 
  inboxEntryDot(_state, getters) {
    const { chatPromoteSwitch, getDotEntry } = getters
    const maiDot = getDotEntry('mail')
    if (!chatPromoteSwitch) {
      return maiDot
    }
    // 
    const aiPromoteDot = getDotEntry('ai_promote')
    const value = maiDot.value || aiPromoteDot.value
    const count = maiDot.count + aiPromoteDot.count
    return {
      value,
      count,
      label: count > 99 ? '99+' : count + ''
    }
  },
  getDotEntry: (state, getters) => key => {
    const { showRedDot = {} } = state
    let dotData = showRedDot[key]
    // 
    if (key === 'inboxEntry') {
      return getters.inboxEntryDot
    }
    if (!dotData) {
      return { value: false, count: 0, label: '0' }
    }
    return {
      value: !!dotData.is_show,
      count: dotData.value,
      label: dotData.value > 99 ? '99+' : dotData.value + ''
    }
  },
  /**
   * vip 
   * levelStr 
   */
  getVipCurrentLevel:
    state =>
    (levelStr = '') => {
      const { user = {} } = state
      const { vip_id = 1, user_config = {} } = user
      // vip_begin_level 10 --> VIP10
      const { vip_begin_level = 0 } = user_config

      const level = levelStr ? levelStr : vip_id

      if (vip_begin_level == 1) return String(level)
      return String(level - 1)
    },
  /**
   * 
   * 
   *  
   *      
   *  
   *      
   *      
   */
  isLuckShow(state) {
    const { lucky_wheel_activity_conf = {} } = state.systemConfig
    const { start_time = 0, end_time = 0 } = lucky_wheel_activity_conf
    const current_time = parseInt(new Date().getTime() / 1000)

    // :
    if (current_time > start_time && current_time < end_time) return true
    // 
    const redDot = state.showRedDot['red_dot_wheel_draw_num'] || {}
    const { is_show = 0, value = 0 } = redDot
    if (is_show > 0) return true

    return false
  },
  /**
   * 
   * bet_loss_gift_switch 0: 1:
   */
  isLossShow(state) {
    const { bet_loss_gift_switch = 0 } = state.systemConfig
    if (bet_loss_gift_switch == 1) return true
    return false
  },
  /**
   * 
   * back_task_switch 0: 1:
   */
  isTaskShow(state) {
    const { back_task_switch: config_back_task_switch = 0 } = state.systemConfig
    const { back_task_switch = 0 } = state.user
    if (config_back_task_switch == 1 && back_task_switch == 1) return true
    return false
  },
  /**
   * -
   * back_task_weekcard_switch 0: 1:
   */
  isTaskSignShow(state) {
    const { back_task_weekcard_switch = 0 } = state.user
    if (back_task_weekcard_switch == 1) return true
    return false
  },
  /**
   * 
   * getUserInfo
   * getUserInfo,commonConfig
   *  -> READMD.md
   * @param {String} key:  01
   */
  getShowConfig:
    state =>
    (key = '') => {
      if (!key) return 0

      const { systemConfig = {}, user = {} } = state
      const { show_config_map: systemCon = {} } = systemConfig
      const { show_config_map: userCon = {} } = user.user_config || {}
      const commonCon = Object.keys(userCon).length > 0 ? userCon : systemCon
      const value = commonCon[key] || 0

      return value
    },
  /**
   * 
   * @param {*} is_official_account 0 !0
   */
  isTourist(state) {
    const { is_official_account = 0 } = state.user
    if (is_official_account == 0) return true
    return false
  },
  /**
   * 
   */
  guidanceSwitch(state, getters) {
    const { reg_time } = state.user
    const { guidance_hall_switch, guidance_hall_switch_open_time } = state.systemConfig
    if (guidance_hall_switch == 0) return false
    if (getters['isTourist']) return true
    return parseInt(reg_time) >= parseInt(guidance_hall_switch_open_time)
  },
  /**
   * /
   * @param {*} withdraw_show 0 !0
   */
  isWithdrawShow(state) {
    const { withdraw_show } = state.user || {}
    if (withdraw_show != 0) return false
    return true
  },
  /**
   * 
   * @param {*} vip_clean_bet_switch 0: 1:
   */
  isBetShow(state) {
    const { vip_clean_bet_switch = 0 } = state.systemConfig || {}

    return vip_clean_bet_switch != 0
  },
  /**
   * 
   */
  getMoreLangText:
    state =>
    (name = '') => {
      const langMoreInfo = state.langMoreInfo
      const currentInfo = langMoreInfo.find(f => f.name == name)
      return currentInfo ? currentInfo.value : name
    },
  /**
   * vip
   * sign_switch 0: 1:
   * sign_popup () 0: 1:
   */
  isVipSignShow(state) {
    const { sign_switch = 0, sign_popup = 0 } = state.systemConfig

    return {
      show: sign_switch == 1,
      popup: sign_popup == 1
    }
  },
  /**
   *  
   * @param {*} default_site_id id
   */
  getCurSiteInfo(state) {
    // 
    const siteItem = state.multiCurrencyList.find(item => item.master == 1)
    // 
    const defaultSite = state.multiCurrencyList.find(item => item.id == item.default_site_id)
    // 
    const localSite = state.multiCurrencyInfo
      ? state.multiCurrencyList.find(item => item.id == state.multiCurrencyInfo.id)
      : null

    const site = localSite ? localSite : defaultSite ? defaultSite : siteItem ? siteItem : state.multiCurrencyList[0]
    return site || {}
  },
  /**
   * -
   * 0 1
   */
  isAgentDateSwitch(state) {
    const { agent_data_switch = 0 } = state.systemConfig

    return agent_data_switch == 1
  }
}

const mutations = {
  ...make.mutations(state),
  SET_USER(state, payload = {}) {
    let userInfo = {}
    if (Object.keys(payload).length > 0) {
      if (payload.user_config) {
        userInfo = payload
      } else {
        const { user = {} } = state
        userInfo = { ...payload, user_config: user.user_config || {} }
      }
    }

    state.user = userInfo
    Storage.set('user', userInfo)
  },
  SET_VISITOR(state, payload) {
    state.user = payload
    Storage.set('visitor', payload)
    Storage.set('user', payload)
  },
  SET_USER_HISTORY(state, payload) {
    let list = state.userHistory
    let t = list.find(user => user.uid == payload.uid)
    if (!t) list.push(payload)
    state.userHistory = list
  },
  SET_CLEAR_USER_HISTORY(state, list = []) {
    state.userHistory = list
  },
  CLEAR_USER_HISTORY(state, payload) {
    let list = state.userHistory
    let t = list.find(user => user.uid == payload.uid)
    if (!t) list.push(payload)
    state.userHistory = list
  },
  SET_LOGOUT(state, payload) {
    state.userHistory[0] = payload
    state.user = payload
    state.visitor = payload
    state.showRedDot = { mail: {} }
    Storage.set('visitor', payload)
    Storage.set('user', payload)
  },
  async SET_LANGUAGE_TYPE(state, payload) {
    state.languageType = payload
    await i18n.setI18nLanguage(payload)
  },
  SET_LOCAL_DOT_VALUE(state, payload) {
    const { key, is_show, value } = payload
    state.showRedDot[key] = {
      is_show,
      value: value || 0
    }
  },
  // 
  SET_BIRTHDAY(state, date) {
    state.user.birthday = date
  },
  SET_GAME_ACTIVITY_LIST(state, list) {
    let gameActivityList = []
    if (Array.isArray(list)) {
      gameActivityList = list.sort((a, b) => b.start_time - a.start_time)
    }
    state.gameActivityList = gameActivityList
  },
  // 
  SET_CAROUSEL_LIST(state, list) {
    state.carouselList = list
  },
  // /
  SET_MULTI_CURRENCY_LIST(state, list) {
    state.multiCurrencyList = list
  },
  // 
  SET_MULTI_CURRENCY_INFO(state, info) {
    state.multiCurrencyInfo = info
    multiSite.set(info)
  }
}

const actions = {
  // 
  logout: ({ state, commit, dispatch }) => {
    return new Promise(async (resolve, reject) => {
      // AJ 
      AJ_DOTTING.getAjReporting()

      const agentid = localStorage.getItem('agentid')
      const params = {
        mainVer: state.mainVer,
        subVer: state.subVer,
        pkgName: state.pkgName,
        os: state.platform,
        deviceid: state.deviceid,
        agentid,
        firstInstall: state.firstInstall,
        Type: state.type,
        language: state.languageType,
        ioswebclip: 0,
        isShell: 0,
        domain: getEnvConfig.baseURL() // 
      }
      commit('SET_IS_FORCED', false)
      let res = {}
      if (state.visitor.token) {
        params.token = state.visitor.token
        res = await getUserInfo(params)
      } else {
        res = await visitorLogin(params)
      }

      if (res.code == 0) {
        // 
        dispatch('responsible/renewRemindStorage', { sign: 'logout', data: state.user }, { root: true })
        // 
        dispatch('source/clearInviteInfo', {}, { root: true })

        commit('SET_LOGOUT', res.data)
        resolve(res)
      } else {
        commit('SET_USER_HISTORY', [])
        commit('SET_USER', {})
      }
      if (res.code != 0) reject(res)
    })
  },
  //  -- 
  async createVisitorAccount({ state, commit }, callback) {
    const agentid = localStorage.getItem('agentid')
    let params = {
      mainVer: state.mainVer,
      subVer: state.subVer,
      pkgName: state.pkgName,
      os: state.platform,
      deviceid: state.deviceid,
      agentid,
      firstInstall: state.firstInstall,
      Type: state.type,
      language: state.languageType,
      ioswebclip: 0,
      isShell: 0,
      domain: getEnvConfig.baseURL() // 
    }
    let { code, data, msg } = await visitorLogin(params).catch(e => e)
    if (code == 0) {
      commit('SET_USER_HISTORY', data)
      commit('SET_VISITOR', data)
    }
    if (callback) callback({ code, data, msg })

    // if(callback) callback({code: 1, data: "account is limited", time: 12123120});
  },
  // 
  async refreshAccount({ state, commit, dispatch }, payload) {
    const agentid = localStorage.getItem('agentid')
    // 
    let callback = null
    let isInitial = false
    if (typeof payload === 'function') {
      callback = payload
    }
    if (typeof payload === 'object') {
      callback = payload.callback
      isInitial = payload.isInitial
    }
    let params = {
      token: state.user.token,
      mainVer: state.mainVer,
      subVer: state.subVer,
      os: state.platform,
      deviceid: state.deviceid,
      agentid,
      firstInstall: state.firstInstall,
      Type: state.type,
      language: state.type,
      ioswebclip: 0,
      isShell: 0
    }
    // pkgName
    if (isInitial) {
      params.pkgName = state.pkgName
    }
    let { code, data, time } = await getUserInfo(params)
    commit('SET_USER_DATA_VERSION', time)
    if (code == 0) {
      if (data && data.apk_info) {
        commit('SET_APK_ACTIVITY_GOLD', data.apk_info) // 
      } else {
        commit('SET_APK_ACTIVITY_GOLD', {
          //  
          apk_send_gold: 0, // 
          apk_show_switch: 0 // 
        }) // 
      }
      if (data.is_official_account == 0) {
        //
        commit('SET_VISITOR', data)
      } else {
        // truefalse 
        if (state.isRefreshUser) {
          commit('SET_USER', data)
        }
      }
    }
    if (callback) callback({ code, data })
  },
  /**
   * 
   * 
   * @param {*} params
   */
  getRegressionDialog({ state, commit, dispatch }, params) {
    return new Promise(async (resolve, reject) => {
      const res = await getRegressionDialog(params).catch(e => e)
      if (!res || res.code != 0) return reject('cancel')

      const resData = res.data && lodash.isObject(res.data) ? res.data : {}
      commit('SET_DIALOG_INFO', resData)

      resolve('ok')
    })
  },
  /**
   * 
   * sign_reward                     // 
   * notice                          // 
   * unclaimed_task                  // 
   * spread_way                      // 
   * clean_bet                       // 
   * user_spread_way                 // 
   * loss_waiver_red                 // 
   * recharge_send_activity          // 
   * vip_cash_gift                   // vip
   * main                            // 
   * week_month_card                 // vip 
   * kf_reply_user_question          // 
   * free_lose
   * ai_promote                       // ai
   * red_dot_wheel_draw_num           // 
   * red_dot_bet_loss_gift            // 
   * back_task                       	// 
   * weekcard_sign                    // -
   */
  showRedDotReceivedFun: ({ state, commit }, params) => {
    return new Promise(async (resolve, reject) => {
      const { code, data } = await showRedDotReceived(params).catch(e => e)
      let { KfNoticeData, showRedDot } = state
      if (code == 0) {
        // 
        const { kf_reply_user_question = {} } = data
        const { value = {} } = kf_reply_user_question
        commit('SET_KF_NOTICE_DATA', Object.assign(KfNoticeData, value))

        commit('SET_SHOW_RED_DOT', { ...showRedDot, ...data })
        resolve('ok')
      } else {
        reject('cancel')
      }
    })
  },
  /**
   * @description 
   * @param { Object } params
   */
  updateLocalDotValue({ commit }, params) {
    const { key, is_show = 0, value = 0 } = params
    if (!key) return
    commit('SET_LOCAL_DOT_VALUE', { key, is_show, value })
  },
  // 
  readDotStatus({ commit, state }, key) {
    if (!key) return
    const params = {
      event: key,
      token: state.user.token,
      language: state.languageType
    }
    // 
    readDotStatus(params),
      // 
      commit('SET_LOCAL_DOT_VALUE', { key, is_show: 0, value: 0 })
  },
  getRechargeGoldFun: ({ state, commit }, params) => {
    return new Promise(async (resolve, reject) => {
      let res = await getRechargeGold(params)
      if (res.code == 0) {
        resolve(res)
      }
    })
  },
  // pixel
  getAjNoticeFun: async ({ state, commit }) => {
    const googlePixelId = getFacebookPixelId()
    const { token } = state.user
    const params = {
      token,
      notice_app_id: googlePixelId,
      is_use_cache: googlePixelId ? 1 : 0
    }
    const res = await getAjNotice(params)
    if (res.code == 0) {
      const { data } = res
      if (data.length > 0) {
        data.forEach(element => {
          // aj 
          AdjustlogEvent(state.systemConfig, element.key, element.value)
          const logParams = { currency: 'USD', value: element.value }
          // 
          // 
          if (element.key == 'recharge') {
            logEvent(EVENT_KEY.completePayment, { params: logParams })
          }
          // 
          if (element.key == 'first_recharge') {
            getFirstChargeNum({ token })
            logEvent(EVENT_KEY.firstPayment, { params: logParams })
          }
        })
      }
      return res
    }
  },
  getWithdrawGoldFun: ({ state, commit }, params) => {
    return new Promise(async (resolve, reject) => {
      let res = await getWithdrawGold(params)
      if (res.code == 0) {
        resolve(res)
      }
    })
  },
  // 
  changeSite: async ({ commit, getters, dispatch, rootState }, item) => {
    const { id, master } = item
    if (getters['getCurSiteInfo'].id == id) return false

    const isTourist = JSON.parse(JSON.stringify(getters['isTourist']))
    /**
     * vuex
     */
    // base
    commit('SET_LOGOUT', {})
    commit('SET_DATA_VERSION', 0)
    commit('SET_USER', {})
    commit('SET_CLEAR_USER_HISTORY', [])
    // common
    commit('common/SET_GET_API_BACKUP_URL_NUB', false, { root: true })
    // source
    commit('source/SET_RECHARGE_USER', {}, { root: true })
    commit('source/SET_WITHDRAW_USER', {}, { root: true })

    // 
    localStorage.clear()
    // 
    commit('SET_MULTI_CURRENCY_INFO', { id, master })

    // name
    const routerName = window.$vm.$route.name || 'menu'

    // 
    getEnvConfig.reset()

    // window.$pop.loading().show()
    dispatch('common/setSiteList', {}, { root: true }).then(_ =>
      dispatch('common/setInitialConfig', {}, { root: true })
        .then(_ =>
          dispatch('common/startUserData', {}, { root: true })
            .then(_ =>
              dispatch('common/getLanguageList', {}, { root: true }).then(_ =>
                dispatch('common/getLanguageInfo', {}, { root: true }).then(_ => {
                  window.$vm.$router.push('/menu')
                  // 
                  dispatch('common/getInit', { name: routerName, isTourist }, { root: true })
                  // key
                  const { updateAppKey = 0 } = rootState.common
                  commit('common/SET_UPDATE_APP_KEY', updateAppKey + 1, { root: true })
                  // 
                  setTimeout(() => {
                    if (!isTourist) dispatch('modal/setLoginRegistShow', {}, { root: true })
                  }, 67)

                  // window.$pop.loading().hide()
                })
              )
            )
            .catch(code => {
              // window.$pop.loading().hide()
              // 
              filterPath(code)
            })
        )
        .catch(code => {
          // window.$pop.loading().hide()
          // 
          filterPath(code)
        })
    )

    return true
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
