/**
 *
 * @param {*} item
 * @param {*} vm this
 * @param {*} isLogin ,my-footer  (official)
 */
export const jump = (item, vm, isLogin = true) => {
  // 
  const isTourist = store.getters['base/isTourist']

  // 
  let outside = item.outside || item.out_url || item.link_url || item.external_link || item.outside_link
  outside = outside && outside.indexOf('http') == -1 ? `//${outside}` : outside
  if (outside) {
    //  || 
    if (!isTourist || !isLogin) {
      // 
      window.open(outside, '_blank')
    } else {
      store.dispatch('modal/setLoginRegistShow')
    }

    return
  }

  // 
  const { inside_data } = item
  if (inside_data) {
    const { type, id, gid, sub_gid, type_id, sub_type_id, activity_type = 1 } = inside_data
    // 
    if (type == 0) {
      if (linkLogin.includes(Number(id))) {
        linkMap[id] && linkMap[id](vm)
      } else {
        if (!isTourist) {
          linkMap[id] && linkMap[id](vm)
        } else {
          store.dispatch('modal/setLoginRegistShow')
        }
      }
      return
    }
    if (!isTourist) {
      // 
      if (type == 1) {
        if (!gid || !sub_gid) return
        if (hasLossLimit(vm)) return
        vm.$pop.game().show({
          gid,
          gameid: sub_gid
        })
      }
      // 
      if (type == 2) {
        if (!type_id || !sub_type_id) return
        gameAPI.finSubCategory(type_id, sub_type_id).then(data => {
          if (data) {
            vm.$router.push({
              name: 'gameChild',
              query: { gameName: data.gameName, factoryIsShow: false, id: data.id }
            })
          }
        })
      }
      // 	
      if (type == 3) {
        if (!gid) return
        if (hasLossLimit(vm)) return
        vm.$pop.game().show({ gid, gameid: 0, is_activity: activity_type })
      }
    } else {
      store.dispatch('modal/setLoginRegistShow')
    }
  }
}
