// 
import FBSignInButton from 'vue-facebook-signin-button'
import GSignInButton from 'vue-google-signin-button'

// 
import '@style/_global/style.scss'

import helper from '@/utils/helper.js'
import animation from '@/utils/animation.js'
import B from '@/utils/base'
import registerComponent from '@/components/dialog/index.js'
import getImage from '@/assets/imgCache.js'
import loadIcon from './assets/icons'

// 
import AutoFont from '@/components/ui/AutoFont'
import IconFont from '@/components/ui/icon-font'
import CustomDialog from '@/components/ui/custom-dialog'

import '@/directive/drag'
import '@/directive/zoom'
import '@/directive/horizontalScroll'
import '@/directive/image'
import '@/directive/zinx'

export default function initGlobal({ Vue, store, router, vuetify }) {
  Vue.use(FBSignInButton)
  Vue.use(GSignInButton)

  // 1. web workertaskworker
  // import './registerServiceWorker';
  const isDebug_mode = process.env.NODE_ENV !== 'production'
  Vue.config.debug = isDebug_mode
  Vue.config.devtools = isDebug_mode
  Vue.config.productionTip = isDebug_mode
  Vue.prototype.$B = B(store)
  Vue.prototype.$u = helper
  Vue.prototype.$anim = animation
  Vue.prototype.$getImage = getImage
  Vue.prototype.$loadIcon = loadIcon

  // 
  Vue.prototype.$log = window.console.log

  // 
  Vue.component('auto-font', AutoFont)
  Vue.component('icon-font', IconFont)
  Vue.component('custom-dialog', CustomDialog)

  // 
  window.$pop = Vue.prototype.$pop = registerComponent(store, router, vuetify)

  Vue.prototype.$sv = {}
}
