import { GAME_DOWN } from './TrackDefine'
import { debounce } from '@/utils/helper'

//track
export function setGameDown(callback) {
  let maxRequestTimes = 4
  setFailRunTask(callback, 2000, maxRequestTimes)
}

/**
 * @desc track
 * @api addTrack track
 *      trackTrackDefine.js
 * @api removeTrack track
 * @api runTrack track
 */
export const EventTrackManager = {
  trackQueue: [],
  addTrack(key, callback) {
    if (!containTrack(key, this.trackQueue)) this.trackQueue.push({ [key]: callback })
    return this
  },
  removeTrack(key) {
    if (containTrack(key, this.trackQueue)) {
      this.trackQueue.splice(getTrackIndex(this.trackQueue, key), 1)
    } else {
      throw new Error(`The specified ${key} is not added to the current track queue!`)
    }
    return this
  },
  getTrack(eventName) {
    return this.trackQueue[getTrackIndex(this.trackQueue, eventName)][eventName]
  },
  runTrack(key) {
    if (containTrack(key, this.trackQueue)) {
      debounce(() => {
        this.trackQueue[getTrackIndex(this.trackQueue, key)][key]()
      }, 2000)
    } else {
      throw new Error(`Event ${key} does not exist and cannot be executed!`)
    }
  }
}

//
function containTrack(key, list) {
  return list.find(v => Object.keys(v).includes(key))
}
function getTrackIndex(list, key) {
  return list.findIndex(v => Object.keys(v).includes(key))
}

/**
 * @desc 5;
 * @params(callback) 
 * @params(mills) 5s
 * @params(times) 5
 */
function setFailRunTask(callback, mills, times) {
  publishTask(callback, mills, times).then(() => {})
}

function runQueueTask(callback, mills) {
  setTimeout(() => {
    callback()
  }, mills)
}

function publishTask(callback, mills, times, idx) {
  let index = !idx ? 1 : idx
  if (index > times) return Promise.resolve()
  return new Promise(resolve => {
    runQueueTask(
      _ =>
        callback()
          .then(resolve)
          .catch(_ => publishTask(callback, mills, times, index)),
      mills
    )
    index += 1
  })
}

/****************************************************************/
/**
 * @desc 
 * @distination 
 * @distination dom
 */
export const catchTrack = {
  tx: EventTrackManager,
  // httptrack
  catchHttpEvent(httpRequest) {
    if (this.tx.trackQueue.length == 0) return () => {}
    let sidx = GAME_DOWN.findIndex(v => v.indexOf(httpRequest) > -1)
    if (sidx != -1) this.tx.getTrack('GAME_DOWN')()
  }
}
