import store from '@/store'
import { isChristma } from '@/api/setting.js'
import { callJava, isNativeClient, callScriptMethod } from '@/utils/helper.js'

// AdJust 
let time = 13
let timeSetInterval = null

// 
export default function createInterceptor() {
  const getAdJustKeyFun = resolve => {
    if (time <= 0) {
      if (timeSetInterval) {
        clearTimeout(timeSetInterval)
        timeSetInterval = null
      }
      resolve()
    } else {
      const getAdJustKey = callJava('getAdJustKey')
      if (getAdJustKey) {
        const getAdJustKeyData = JSON.parse(getAdJustKey)
        store.commit('base/SET_AD_JUST_KEY', getAdJustKeyData)
        time = 0
        if (timeSetInterval) {
          clearTimeout(timeSetInterval)
          timeSetInterval = null
        }
        resolve()
      } else {
        time--
        timeSetInterval = setTimeout(() => {
          getAdJustKeyFun(resolve)
        }, 1000)
      }
    }
  }
  // 
  const initNativeEnv = resolve => {
    if (!window.android) return resolve()
    // 
    callScriptMethod()

    // 
    const pkgname = callJava('getPackageName')
    // 
    const version = callJava('getApkVersion')
    store.commit('base/SET_PKG_NAME', pkgname)
    store.commit('base/SET_NATIVE_VER', version)
    const adJustKey = store.getters['base/getAdJustKey']
    if (!adJustKey) {
      getAdJustKeyFun(resolve)
    } else {
      resolve()
    }
  }

  return new Promise(resolve => {
    time = 13
    if (timeSetInterval) {
      clearTimeout(timeSetInterval)
      timeSetInterval = null
    }

    // logo
    if (isChristma) {
      const christmasLogoId = document.getElementById('christmasLogoId')
      const startLogoIds = document.getElementById('startLogoIds')

      if (christmasLogoId) christmasLogoId.remove()
      if (startLogoIds) startLogoIds.style.display = 'block'
    }
    // 
    if (isNativeClient()) {
      initNativeEnv(resolve)
    } else {
      resolve()
    }
  })
}
