/**
 * @desc: 
 * */
import { make } from 'vuex-pathify'
import { getSecondRechargeExtAct } from '@/api/modules/config'
import { Storage } from '@/utils/helper'
import { getActivityConfig } from '@/api/modules/agent.js'
const state = {
  rechargeUser: {}, // recharge
  withdrawUser: {}, // withdraw

  gameCollection: Storage.get('gamelist'), // 
  tablist: [], // 

  lobbylist: [], // 
  gameInfo: [], // 
  gameType: [], // 
  otherFactory: [], // 
  gamelist: [],
  languageList: [],
  recent: [],
  games: [],
  collectGames: [],
  phonePlaces: [],
  countryGameLogo: [],
  deskNotices: [],
  gameFactory: {},
  maintains: [], // 
  pictureUrl: '', //
  /**  */
  experience_list: [], // 
  furyInfo: {}, // 
  isRotate: false, // 
  isToGame: false, // 
  pageParameter: {}, // 
  rechargeInfo: {}, // v3
  withdrawShare: false, //  -- 
  activity: null, //  v2
  activityInvite: {
    // 
  },
  inviteResult: {
    // 
  },
  inviteGiftGold: 0, // 
  reportLastTime: {}, //  
  secondRechargeList: [] // 
}

const getters = {
  //ID
  //return true: 
  getExperienceAuth(state) {
    return (gid, gameid) => {
      let l = state.experience_list.filter(v => v.gid == gid)
      if (gameid) l = l.filter(v => v.gameid.includes(gameid))
      return l.length == 0 ? false : true
    }
  }
}

const mutations = make.mutations(state)

const actions = {
  // 
  getActivityConfigFn: async ({ state, commit }, params) => {
    let res = await getActivityConfig(params)
    if (res.code == 0) {
      commit('SET_ACTIVITY_INVITE', res.data)

      return res
    }
  },
  // 
  getSecondRechargeExtActFn: ({ state, commit }, params) => {
    return new Promise(async (resolve, reject) => {
      let res = await getSecondRechargeExtAct(params)
      if (res.code == 0) {
        resolve(res)
      }
    })
  },
  // 
  clearInviteInfo({ state, commit }) {
    commit('SET_ACTIVITY_INVITE', { ...state.activityInvite, activity_user_task: null })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
