import { make } from 'vuex-pathify'

// 
const state = {
  depositWithdrawShow: false, // 
  depositWithdrawStatus: 'deposit', // depositwithdraw
  withdrawEntrance: 0, //  0: 1: 2:
  updateWithdrawType: false, // 
  updateDepositType: false, // 
  loginRegistShow: false, // 
  loginRegistStates: true, //  true false 
  emailState: false, // 
  leftMenu: false, // 
  rightMenu: false, // 
  isPromotionKefu: false // 
}

const getters = {}

const mutations = {
  ...make.mutations(state),
  // 
  SET_DEPOSIT_WITHDRAW_SHOW(state, status = false) {
    state.depositWithdrawShow = status
  },
  // tab
  SET_DEPOSIT_WITHDRAW_STATUS(state, status = 'deposit') {
    state.depositWithdrawStatus = status
  },
  // 
  SET_WITHDRAW_ENTRANCE(state, status = 0) {
    state.withdrawEntrance = status
  },
  // 
  SET_UPDATE_DEPOSIT_TYPE(state, status = false) {
    state.updateDepositType = status
  },
  // 
  SET_UPDATE_WITHDRAW_TYPE(state, status = false) {
    state.updateWithdrawType = status
  },
  // 
  SET_LOGIN_REGIST_SHOW(state, status = false) {
    state.loginRegistShow = status
  },
  //  true false 
  SET_LOGIN_REGIST_STATES(state, status = true) {
    state.loginRegistStates = status
  },
  // 
  SET_EMAIL_STATE(state, status = false) {
    state.emailState = status
  },
  // 
  SET_LEFT_MENU(state, status = false) {
    state.leftMenu = status
  },
  // 
  SET_RIGHT_MENU(state, status = false) {
    state.rightMenu = status
  },
  // 
  SET_IS_PROMOTION_KEFU(state, status = false) {
    state.isPromotionKefu = status
  }
}

const actions = {
  // /tab/
  setDepositWithdrawShow({ commit }, { show = true, tab = 'deposit', entrance = 0 } = {}) {
    commit('SET_DEPOSIT_WITHDRAW_SHOW', show)
    commit('SET_DEPOSIT_WITHDRAW_STATUS', tab)
    commit('SET_WITHDRAW_ENTRANCE', entrance)
  },
  // /tab
  setLoginRegistShow({ commit }, { show = true, tab = true } = {}) {
    commit('SET_LOGIN_REGIST_SHOW', show)
    commit('SET_LOGIN_REGIST_STATES', tab)
  },
  // 
  setEmailState({ commit }, show = false) {
    commit('SET_EMAIL_STATE', show)
  },
  // 
  setLeftMenu({ commit }, show = false) {
    commit('SET_LEFT_MENU', show)
  },
  // 
  setRightMenu({ commit }, show = false) {
    commit('SET_RIGHT_MENU', show)
  },
  // 
  setIsPromotionKefu({ commit }, show = false) {
    commit('SET_IS_PROMOTION_KEFU', show)
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
