import { make } from 'vuex-pathify'
import { $t } from '@/utils/i18n.js'
import { POP_TIME } from '@/api/setting.js'
import { lodash, callJava } from '@/utils/helper.js'
import { gameUp, gameDown } from '@/api/modules/game.js'
import { hasLossLimit } from '@/utils/hasLossLimit.js'
import globalStore from '@/store'
import PublishSubscribe from 'publish-subscribe-js'

const state = {
  gameItem: {}, // 
  gameOrigin: 1, //  0:H  1:V
  isActivity: false, //  0  1 
  openGameMode: 0 //  0:iframe 1:/9w/wickets9
}

const getters = {}

const mutations = {
  ...make.mutations(state)
}

const actions = {
  // 
  getGameReset: ({ state, commit, dispatch }) => {
    commit('SET_GAME_ITEM', {})
    commit('SET_GAME_ORIGIN', 1)
    commit('SET_IS_ACTIVITY', false)
    commit('SET_OPEN_GAME_MODE', 0)
    // 
    globalStore.commit('source/SET_IS_TO_GAME', false)
  },
  /**
   * 
   * game 
   * manufacturer 
   */
  enterTheGame: ({ state, commit, dispatch }, { game = {}, vm = {}, manufacturer = {} }) => {
    if (!(lodash.isObject(game) && Object.keys(game).length > 0)) return
    if (!vm) return

    // 
    const { factoryIsShow = false } = manufacturer
    /**
     * has_child: 
     * gameName: 
     * id: id
     * gid: id
     */
    const { has_child = 0, gameName = '', id = 0, gid = 0 } = JSON.parse(JSON.stringify(game))

    if (has_child == 1) {
      /**
       * 
       */
      vm.$store.commit('source/SET_PAGE_PARAMETER', game)
      vm.$router.push({ name: 'gameChild', query: { gameName, factoryIsShow, id } })
    } else {
      /**
       * 
       * total_recharge 
       */
      const { user = {}, systemConfig = {} } = vm.$store.state.base || {}
      const { total_recharge = 0 } = user
      const { ext_game_recharge_limit = {} } = systemConfig

      // 
      if (vm.$store.getters['base/isTourist']) return vm.$store.dispatch('modal/setLoginRegistShow')

      // 
      if (total_recharge < parseFloat(ext_game_recharge_limit[gid] || 0)) {
        return vm.$pop
          .confirm({
            title: '',
            content: vm.$t('SC_LessRecChips', { S1: ext_game_recharge_limit[gid] }),
            confirmText: vm.$t('General.General_1'),
            cancelText: vm.$t('General.General_2'),
            success: () => {
              vm.$store.dispatch('modal/setDepositWithdrawShow')
            }
          })
          .show()
      }

      // 
      if (hasLossLimit(vm)) return

      // 
      vm.$pop.game().show(game)
    }
  },
  /**
   * 
   * game 
   */
  getGemeUp: ({ state, commit, dispatch }, game = {}) => {
    return new Promise(async (resolve, reject) => {
      const { user = {}, languageType = '', isApp = false } = globalStore.state.base || {}

      // 
      commit('SET_GAME_ITEM', game)
      // 
      globalStore.commit('source/SET_IS_TO_GAME', true)

      /**
       * gid          id
       * gameid
       * game_show     0:H  1:V
       * is_activity   0  1 
       * open_mode     0:iframe 1:/9w/wickets9
       */
      const { gid = 0, gameid = 0, game_show = 1, is_activity = 0, open_mode = 0 } = state.gameItem
      // 
      commit('SET_GAME_ORIGIN', game_show)

      let params = {
        token: user.token,
        language: languageType,
        gid,
        sub_gid: gameid,
        direction: game_show,
        type: isApp ? 100 : 101
      }
      if (is_activity) params.is_activity = is_activity

      $pop.loading().show()
      const res = await gameUp(params).catch(e => e)
      $pop.loading().hide()
      if (!res || res.code != 0) {
        $pop.message().show({ content: res && res.msg, timeout: 5000 })
        // 
        dispatch('getGameReset')

        return reject('cancel')
      }

      /**
       * html                  iframe -> html
       * base_url              iframe -> url
       * gameGid              id
       * activity_template    FC=1 2 0
       */
      const resData = res.data && lodash.isObject(res.data) ? res.data : {}
      const { html = '', base_url = '', gid: gameGid = 0, activity_template = 0 } = resData

      /**
       * 
       * FC && 
       * activity_templateFC=1 2 0
       * is_activity 0  1 
       */
      commit('SET_IS_ACTIVITY', activity_template == 1 && is_activity == 1)
      // 
      commit('SET_OPEN_GAME_MODE', open_mode)
      // 
      if (state.gameOrigin != 1) callJava('setOrientation', { dir: 'H' })

      resolve({
        html,
        base_url,
        gameGid,
        open_mode
      })
    })
  },
  /**
   * 
   */
  getGemeDown: async ({ state, commit, dispatch }) => {
    const { isForced = false, user = {}, languageType = '' } = globalStore.state.base || {}

    // 
    dispatch('getGameReset')
    // 
    if (state.gameOrigin != 1) callJava('setOrientation', { dir: 'V' })

    // 
    if (isForced) {
      //  confirm 
      setTimeout(() => {
        $pop
          .confirm({
            title: $t('General.General_9'),
            content: $t('UI.System_43'),
            confirmText: $t('General.General_1'),
            closeIcon: false,
            success: async () => {
              // const res = await gameDown({ token: user.token, language: languageType }).catch((e) => e);
              // if (!res || res.code != 0) $pop.message().show({ content: res && res.msg });

              globalStore.dispatch('base/logout').then(() => {
                dispatch('modal/setLoginRegistShow', {}, { root: true })
              })
            }
          })
          .show()
      }, POP_TIME + 200)

      return
    }

    // 
    globalStore.commit('source/SET_IS_ROTATE', true) // 
    const res = await gameDown({ token: user.token, language: languageType }).catch(e => e)
    if (!res || res.code != 0) $pop.message().show({ content: res && res.msg })

    // vipvip, 
    setTimeout(() => {
      // 
      PublishSubscribe.publish(globalStore.state?.dialog?.DUALOG_MANAGER_KEY, { key: 'remind', seat: 'first' })

      // 
      Promise.all([
        globalStore.dispatch('base/showRedDotReceivedFun', {
          version: 2,
          event: 'clean_bet',
          language: languageType,
          token: user.token
        }),
        globalStore.dispatch('base/showRedDotReceivedFun', {
          version: 2,
          event: 'vip_cash_gift',
          language: languageType,
          token: user.token
        }),
        globalStore.dispatch('base/refreshAccount')
      ]).then(() => {
        // 
        globalStore.commit('source/SET_IS_ROTATE', false)

        // vipvip
        PublishSubscribe.publish(globalStore.state?.dialog?.DUALOG_MANAGER_KEY, { key: 'relegation' })
        PublishSubscribe.publish(globalStore.state?.dialog?.DUALOG_MANAGER_KEY, { key: 'vipReward' })
        PublishSubscribe.publish(globalStore.state?.dialog?.DUALOG_MANAGER_KEY, { key: 'betReward' })
      })
    }, 2 * 1000)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
